import React, { useState } from 'react';
import MenuIcon from '@mui/icons-material/Menu';
import './Movie_Loginbar.css';
import {Button} from "@mui/material";  //    

const Header = () => {
    const [isOpen, setMenu] = useState(false);

    const toggleMenu = () => {
        setMenu(prev => !prev);
    };

    function login() {
        const memberId = document.getElementById("username").value;
        const memberPw = document.getElementById("password").value;

        fetch("/ttt/api/login", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ memberId, memberPw })
        })
            .then(async res => {
                const token = res.headers.get("Authorization");

                if (res.ok && token) {
                    alert(token.toString());
                    localStorage.setItem("jwt", token);
                    alert(" ! JWT ");
                    window.location.href = "/";
                } else {
                    const error = await res.text();
                    alert(" : " + error);
                }
            })
            .catch(err => alert(": " + err));
    }

    return (
        <div className="header bg-dark text-white d-flex justify-content-between align-items-center p-3">
            <h5 className="m-0">My Movie App</h5>

            {/*    */}
            <MenuIcon
                onClick={toggleMenu}
                style={{ cursor: 'pointer', color: 'white' }}
            />

            {/*    */}
            <ul className={`side-menu ${isOpen ? 'show-menu' : 'hide-menu'} bg-secondary text-white`}>
                <li className="py-2 border-bottom px-3"><input type="text" id="username" placeholder="" /></li>
                <li className="py-2 border-bottom px-3"><input type="password" id="password" placeholder="" /></li>
                <li className="py-2 border-bottom px-3"><button onClick={login}></button></li>
                <li className="py-2 border-bottom px-3"><button onClick="join()"></button></li>
                <li className="py-2 border-bottom px-3"><button onClick="logout()"></button></li>
                <Button
                    variant="contained"
                    color="secondary"
                    size="small"
                    onClick={toggleMenu}
                ></Button>
            </ul>
        </div>
    );
};

export default Header;
