import axios from "axios";
import {useParams} from "react-router";
import {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import YouTube from "react-youtube";


function Detail(){
    const { id } = useParams();
    const [movie, setMovie] = useState(null);

    useEffect(() => {
        const options = {
            method: 'GET',
            url: 'https://api.themoviedb.org/3/movie/'+id,
            params: {
                append_to_response: 'videos',
                language: 'ko-KR'
            },
            headers: {
                accept: 'application/json',
                Authorization: 'Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiI2YjA3MGM5ODk0MTY0MTFhNTEzYzRjNmM3OGFmMDc2OSIsIm5iZiI6MTc0NzIxODk4NS44Miwic3ViIjoiNjgyNDcyMjk0OTMzZmUxNDU1MzMzZjM4Iiwic2NvcGVzIjpbImFwaV9yZWFkIl0sInZlcnNpb24iOjF9.rBrd8bY3jM-JG5YfEsj_A6ApxP3KpzZQpw9yyGnRuUw'
            }
        };
        axios
            .request(options)
            .then(res => setMovie(res.data))
            .catch(err => console.error(err));


    },[id]);
    if(movie!=null)
        alert(JSON.stringify(movie.videos));
    if (!movie) return <div className="text-light"> ...</div>;
    return (<div className="bg-black text-light min-vh-100 py-5">
            <div className="container">
                <div className="d-flex">
                    <img src={`https://image.tmdb.org/t/p/w300${movie.poster_path}`} alt={movie.title} className="me-4" />
                    <div>
                        <h2>{movie.title}</h2>
                        <p><strong>:</strong> {movie.overview}</p>
                        <p><strong>:</strong> {movie.release_date}</p>
                        <p><strong>:</strong> {movie.vote_average}</p>
                    </div>
                </div>
                <YouTube
                    videoId = {movie.videos?.results[0]?.key }
                    //opts():         .
                    //  .
                    opts={{
                        width: "560",
                        height: "315",
                        playerVars: {
                            autoplay: 0,
                        },
                    }}
                    // 
                    onEnd={(e)=>{e.target.stopVideo(0);}}
                />
                <Link to={-1}><button></button></Link>
            </div>
        </div>
    );
}

export default Detail;